
#logo{
	height:  30px;
	/*cursor: pointer;*/
}
#arrange-room{
	background-color: #c7eae1;
	background-image: url("./img/grid2.png");
	height: 100vh;
	left: 25px;
}
#furnituresDiv{
	overflow-y: scroll;
	height: 90vh;
	width: 20%;
	float: left;
}


.room{
	background-image: url("/images/floor.jpg");
	background-size: 100% 100%;
}
h6{
	font-weight: bold;
	margin-bottom: 0;
}
p{
	font-size: 0.8rem;
	font-weight: 500;
	margin-bottom: 3px;
}
#ruler{
	height: 90vh;
	overflow: hidden;
	width: 80%;
}
.ruler_y{
	position: relative;
	width: 25px;
	float: left;
}
.img-size{
	height: 100%;
	width: 100%;
}
.header{
	text-align: center;
	padding:18px 20px;
	border-bottom: 2px solid black;
	background-color: #fdf4cf;
	position: -webkit-sticky;
	position: sticky;
}
#bg1{
	/*background-image: url("/images/bg1.jpg");*/
	background-color: red;
}

/*----------google font-------------------*/
body{
	font-family: 'Montserrat', sans-serif;
}
